export interface IGood {
	_id?: string;
	serialNumber: number;
	title: string;
	description: string;
	price?: number;
	tags: string[];
	active: boolean;
}

export class Good implements IGood {
    _id?: string;
	serialNumber: number;
	title: string;
	description: string;
	price: number;
	tags: string[];
	active: boolean = true;

	constructor(object?: IGood){
		if(object) {
			this.setParams(object);
		}
	}

	setParams(object: {
		_id?: string;
		serialNumber?: number;
		title?: string;
		description?: string;
		price?: number;
		tags?: string[];
		active?: boolean;
	}) {
		this._id = object._id || null;
		this.serialNumber = object.serialNumber || this.serialNumber;
		this.title = object.title || null;
		this.description = object.description || null;
		this.price = object.price || 0;
		this.tags = object.tags || [];
		this.active = object.active || false;
	}

	clear() : void {
		this._id = null;
		this.title = null;
		this.description = null;
		this.price = 0;
		this.tags = [];
		this.active = false;
	}
}



